.note_management {
    padding: 20px 20px 40px 20px;
    width: 100%;
    max-width: 1050px;
    margin: 30px auto;
    font-family: serif;
    background: url(../img/chalkboard_bg.png);
    min-height: 600px;
    border: tan solid 12px;
    border-top: #bda27e solid 12px;
    border-left: #b19876 solid 12px;
    border-bottom: #c9ad86 solid 12px;
    box-shadow: 0px 0px 6px 5px rgba(58, 18, 13, 0), 0px 0px 0px 2px #c2a782, 0px 0px 0px 4px #a58e6f, 3px 4px 8px 5px rgba(0, 0, 0, 0.5);
}

@media screen and (max-width: 600px) {
    .main{
        padding: 0px!important;
    }
    .note_management{
        margin: 0px;
    }
    input.ant-input{
        width: 80%!important;
    }
    input.ant-input[name="title"]{
        width: 100%!important;
    }
    .big_title{
        font-size: 40px!important;
    }
    .note_management{
        padding: 12px;
    }
    .font-btn{
        bottom: -40px!important;
    }
}


.big_title{
    font-size: 60px;
    line-height: 85px;
    color: #ebebeb;
    font-family: chalk_font;
    margin-bottom: 0px;
}

.small_title{
    font-size: 30px;
    color: #ebebeb;
    font-family: chalk_font;
    margin-bottom: 0px;
}

.chalkboard_font {
    color: #ebebeb;
    font-size: 20px;
    font-family: chalk_font;
}

@font-face {
    font-family: 'chalk_font';
    src: url('../../font/chalk_font.ttf')  format('truetype'),
         url('../../font/chalk_font.woff') format('woff');
  }